// Colors app

$color-body: #BCE4EE;
$white: #fff;
$blue-dark: #4285F4;
$orange-light: #FFB251;
$orange-darker: #F15A24;

// size
$s-size: 1.2rem;
$m-size: 1.6rem;
$l-size: 3.2rem;
$xl-size: 4.8rem;

// all elements
$font-s: $m-size;
$font-w: 400;
$margin-left: 2%;
$margin: 0 3%;

$size_div: 98%;
$size-header: 5rem;

$radius: .2em;
$left-padding-input: 40%;

// input marge
$pading_input: .5em;


// -- breakpoint ------------------------------------------------------------
$desctop-breakpoint: 45rem;



