@import "../base/settings";

body {
  background-color: #F77006; }

a {
  color: black;
  text-decoration: none; }

h1 {
  text-align: center; }

.patient_name {
  background-color: #F1B972;
  padding: 1em;
  border-radius: $radius;
  border: black solid 1px; }

.end_contatc_info {
  margin: 2em; }

.hr_remarque {
  border-top: solid black 4px;
  width: 80%;
  padding: 0;
  margin-bottom: 1em; }

// important
form:nth-child(2) hr {
  display: none; }
.patient, .log {
  label {
    margin-left: $pading_input;
    font-size: $l-size * .5;
    margin-bottom: 0.2em; }

  input {
    width: 100%;
    padding: .1em;
    padding-left: $pading_input;
    font-size: $l-size * .5;
    border-radius: $radius;
    border: none; }

  input, textarea {
    font-size: $m-size;
    padding: $pading_input;
    font-weight: 400;
    border-radius: $radius; } }

// container
.all_remarques, .info, .secu, .contact {
  border-radius: 0% 0% $radius $radius;
  border: solid black 1px;
  border-top: none;
  background-color: $orange-light; }

//title
.title {
  padding: 1.5em;
  h2 {
    margin: 0 auto;
    text-align: center;
    border-bottom: solid black 1px;
    width: 80%;
    padding-bottom: 1em; } }

// inline el

.inline--el {
  margin: $margin; }

.inline--el__componente {
  margin-bottom: .7em;
  width: $size_div;

  label {
    display: block;
    margin-left: $pading_input;
    font-size: $xl-size * .35;
    margin-bottom: 0.2em; }

  input, textarea {
    width: 99%;
    border-radius: $radius;
    border: none;
    outline: none; }

  textarea {
    height: 6em; } }

.save_button {
  text-align: center;
  padding: 2% 2% 3%;
  button {
    background-color: #09BE33;
    font-size: $m-size;
    padding: $s-size;
    //border: solid black 1px
    border-radius: $radius; } }

.btn_end {
  width: 45%;
  display: inline-block; }

.delete_btn {
  button {
    background-color: crimson;
    color: white; } }

.log {
  padding-top: 5em;
  input {
    font-size: $l-size; }
  label {
    text-align: left;
    font-size: 2.5em; } }



