@import "../base/settings";


.DateInput_fang {
 display: none; }

.picker {
  margin: $margin;
  width: $size-div / 2;
  margin-bottom: .7em;
  label {
    display: block;
    margin-left: 3.6%;
    font-size: $xl-size * .35;
    margin-bottom: 0.2em; } }


.SingleDatePickerInput__withBorder {
  border: none; }

.SingleDatePickerInput {
  background: transparent; }


.DateInput {
  background: transparent;
  width: 100%; }

.DateInput_input {
  border-radius: $radius;
  padding: $pading_input;
  font-size: $m-size;
  font-weight: 400;
  color: black; }


.DateInput_input__focused {}

.CalendarDay {
  font-size: 18px; }

.CalendarMonth_caption {
  font-size: 20px; }

.CalendarDay__selected {
  background-color: #F15A24;
  color: white;
  border: none; }

.DayPicker__withBorder {
  //border-radius: 5px
  border: solid black; }



