@import "../base/settings";

.log-form {
  background-color: #FFA469;
  min-height: 25em;
  border-radius: $radius;
  padding: $l-size $xl-size;
  text-align: center;
  border: solid black 1.5px; }


.log-page--input {
  font-size: $xl-size * .9;
  padding-left: $left-padding-input;
  border-radius: $radius /4;
  display: block;
  margin: .5em auto 0 auto;
  border: none;
  outline: none; }

.log-page--button {
  margin-top: $l-size;
  background-color: #09BE33;
  border: none;
  color: white;
  min-height: $l-size * 2;
  min-width: $xl-size * 3;
  border-radius: $radius;
  font-size: 2em;
  outline: none; }



